
import { CompareType } from '../enum/compare-type.enum';
import { ValueType } from '../enum/value-type.enum';
import { RelationType } from '../enum/relation-type.enum';

export class SearchHandler{
    convert(qc) {
        const tv = (qc.value);
        return Object.keys(tv).length > 0 && tv.value ? [{
            'FilterField' : qc.labelCode,
            'Compare' : CompareType.Like, //like CompareType.like undefined
            'Value' : tv.value,
            'Relation' : RelationType.And, //and undefined
            'Expresstype':ValueType.Value
        }] : [];
    }
}